body {
font: 20px "Century Gothic", Futura, sans-serif;
margin: 20px;
background: #048
}
h2 {
font: 40px "Century Gothic", Futura, sans-serif;
margin: 20px;
color: #fff
}

ol, ul {
padding-left: 30px;
}

.board-row:after {
clear: both;
content: "";
display: table;
}
.container {
    display: grid;
    grid-template-columns:  300px 300px 300px 300px;
}
.item {
    background: #0bd;
    color: #fff;
    padding: 10px;
}
textarea{
width:300px;
height:150px;
font-size:12px;
resize:none;
}

